import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, FormGroup, Row } from 'reactstrap';
import { EditForm, EditViewProps, ERROR, findById, InputGroupInline, InputType, isEmpty, refToFormat, showToast, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { CLASS_NAME, EmpresaMCG } from '../../../../models/dtos/mcg/empresaMCG';
import { CENTRO_CUSTO_GER_TIPO_LANCAMENTO_CENTRO_CUSTO_ENUM_VALUES } from '../../../../models/enumerated/ger/centroCustoGERTipoLancamentoCentroCustoEnum';
import { PLANO_CONTA_GER_TIPO_LANCAMENTO_CONTA_ENUM_VALUES } from '../../../../models/enumerated/ger/planoContaGERTipoLancamentoContaEnum';
import { TIPO_DESCRICAO_DEMONSTRACAO_ENUM_VALUES } from '../../../../models/enumerated/ger/tipoDescricaoDemonstracaoEnum';
import { LALURB_MCG_TIPO_EMISSAO_ENUM_VALUES } from '../../../../models/enumerated/mcg/lalurBMCGTipoEmissaoEnum';
import { atualizarGlobalParameter } from '../../../../services/ger.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState, setGlobalParameter } from '../../../../store/ducks/global.duck';
import LancamentoMCGConfigIS from '../../../components/mcg/lancamentoMCGConfigIS/LancamentoMCGConfigIS';

interface ConfigEmpresaMCGEditViewProps extends EditViewProps<EmpresaMCG> {}

const ConfigEmpresaMCGEditView: FC<ConfigEmpresaMCGEditViewProps & RouteComponentProps> = props => {
  const { reset, ...formProps } = useForm<EmpresaMCG>({ mode: 'onBlur' });

  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const dispatch = useDispatch();

  useEffect(
    () => {
      if (!isEmpty(globalReducer.globalParameter.empresaMCG)) {
        findById({ id: globalReducer.globalParameter.empresaMCG.id }, CLASS_NAME, {
          errorFunction: mensagem => {
            showToast(mensagem, ERROR);
          },
          thenFunction: result => {
            reset(result);
          },
        });
        setTimeout(() => {
          formProps.triggerValidation();
        }, 500);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = (): string[] => {
    const errors = [];
    const [mesRefAtual, anoRefAtual] = refToFormat(formProps.getValues().refAtual).split('/');
    const [mesRefFinal, anoRefFinal] = refToFormat(formProps.getValues().refFinal).split('/');

    if (anoRefAtual > anoRefFinal) {
      errors.push('Referência Atual não pode ser maior que a Referência Final');
    }

    if (anoRefAtual === anoRefFinal) {
      if (mesRefAtual > mesRefFinal) {
        errors.push('O mês da Referência Atual não pode ser maior que o mês da Referência Final');
      }
    }

    if (formProps.getValues().percCompensacao > 100) {
      errors.push('A compensação não pode ser maior que 100%');
    }

    return errors;
  };

  const afterPersist = (): void => {
    atualizarGlobalParameter(
      { id: globalReducer.globalParameter.usuario.id },
      {
        thenFunction: globalParameter => {
          dispatch(setGlobalParameter(globalParameter));
        },
      }
    );
  };

  const defaultWidth = 290;

  return (
    <WindowComponent
      title="Manutenção de Configuração de Empresa"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={4291}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras} afterPersist={afterPersist}>
        <InputGroupInline formProps={formProps} id="empresa.id" hidden={true} />
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>Período Fiscal</CardTitle>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { required: true } }}
                      isPrependLabel={true}
                      id="refAtual"
                      label="Referência Atual"
                      type={InputType.REFERENCE}
                    />
                  </Col>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { required: true } }}
                      isPrependLabel={true}
                      id="refFinal"
                      label="Referência Final"
                      type={InputType.REFERENCE}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput type="switch" id="idsomenteLivroCaixa" name="somenteLivroCaixa" label="Somente Livro Caixa" innerRef={formProps.register} />
          </Col>
        </FormGroup>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              id="tipoLancamentoConta"
              inputDropDownProps={{
                optionsProvider: PLANO_CONTA_GER_TIPO_LANCAMENTO_CONTA_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              isPrependLabel={true}
              label="Tipo Lançamento de Plano Contas"
              labelWidth={defaultWidth}
              placeholder="Selecione o tipo Lançamento de Plano Contas"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              id="tipoLancamentoCentroCusto"
              inputDropDownProps={{
                optionsProvider: CENTRO_CUSTO_GER_TIPO_LANCAMENTO_CENTRO_CUSTO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              isPrependLabel={true}
              label="Tipo Lançamento de Centro Custo"
              labelWidth={defaultWidth}
              placeholder="Selecione o tipo Lançamento de Centro Custo"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              id="tipoDescricaoDesmonstracao"
              inputDropDownProps={{
                optionsProvider: TIPO_DESCRICAO_DEMONSTRACAO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              isPrependLabel={true}
              label="Tipo de Descrição da Demonstração"
              labelWidth={defaultWidth}
              placeholder="Selecione o tipo de Descrição da Demonstração"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <LancamentoMCGConfigIS id="configLancamento" formProps={formProps} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>Lalur</CardTitle>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="emissaoLalur"
                      label="Emissão de Lalur"
                      isPrependLabel={true}
                      placeholder="Selecione o tipo de Emissão de Lalur"
                      formProps={{ ...formProps, validation: { required: true } }}
                      inputDropDownProps={{
                        optionsProvider: LALURB_MCG_TIPO_EMISSAO_ENUM_VALUES,
                        selectProps: { isClearable: false },
                      }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                  <Col>
                    <InputGroupInline
                      id="nroAnoCompensacao"
                      label="Anos para Compensação"
                      isPrependLabel={true}
                      placeholder="Informe os Anos para Compensação"
                      formProps={{ ...formProps, validation: { maxLength: 4, required: true } }}
                      type={InputType.NUMBER}
                    />
                  </Col>
                  <Col>
                    <InputGroupInline
                      id="percCompensacao"
                      label="Compensação"
                      isPrependLabel={true}
                      placeholder="Informe a Compensação"
                      formProps={{ ...formProps, validation: { maxLength: 3, required: true } }}
                      type={InputType.NUMBER}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigEmpresaMCGEditView;
