import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mcg/lancamentoMCGConfig';

const LancamentoMCGConfigIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      formProps={{ ...props.formProps }}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'descricao',
            text: 'Descriçao',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'LancamentoMCGConfigSearchView',
      }}
      label={props.label ? props.label : 'Configuração Padrão de Lançamento'}
      placeholder={props.placeholder ? props.placeholder : 'Informe a Configuração Padrão de Lançamento'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default LancamentoMCGConfigIS;
