import { Basic } from 'summer';

export interface LancamentoMCGConfig extends Basic {
  descricao?: string;
  exibirSaldoContas?: boolean;
  fixarCentroCustoCredito?: boolean;
  fixarCentroCustoDebito?: boolean;
  fixarContaCredito?: boolean;
  fixarContaDebito?: boolean;
  fixarData?: boolean;
  fixarHistoricoPadrao?: boolean;
  fixarHistoricoVariavel?: boolean;
  /** este campo utiliza o enum LancamentoMCGFormaEnum */
  formaLancamento?: number;
  /** este campo utiliza o enum LancamentoConfigHistoricoTypeEnum */
  historico?: number;
  informarLancamentoEncerramento?: boolean;
  /** este campo utiliza o enum TipoBloqueioLancamentoEnum */
  tipoBloqueioLancamento?: number;
  utilizarCentroCusto?: boolean;
  utilizarCentroCustoAtivoPassivo?: boolean;
  utilizarHistoricoPadrao?: boolean;
  utilizarHistoricoVariavel?: boolean;
  utilizarLancPadrao?: boolean;
  /** este campo utiliza o enum lancamentoMCGTipoValidLancPartidaSimples */
  validacaoLancPartidaSimples?: string;
  validarLancReceitaDespesa?: boolean;
}

export const CLASS_NAME = 'br.com.projetusinformatica.calima.mcg.model.dto.lancamentomcgconfig';
